import s from "./Header.module.css";
import { NewAppBar } from "../Head/MyNavbar/AppBar";
import { motion, Variants } from "framer-motion";
import { Link } from "react-router-dom";

export const staggerContainer: Variants = {
  initial: {},
  animate: {
    transition: {
      staggerChildren: 0.5,
    },
  },
};

export const fromLeftFade: (
  direction?: "left" | "right",
  shiftValue?: number,
  duration?: number
) => Variants = (
  direction: "left" | "right" = "left",
  shiftValue: number = 100,
  duration: number = 1
) => {
  return {
    initial: {
      x: direction === "left" ? -shiftValue : shiftValue,
      opacity: 0,
    },
    animate: {
      x: 0,
      opacity: 1,
      transition: {
        duration,
        type: "spring",
      },
    },
  };
};

export const featureImageScale: Variants = {
  initial: {
    scale: 0.2,
    opacity: 0,
  },
  animate: {
    scale: 1,
    opacity: 1,
    transition: {
      duration: 1.5,
      ease: "easeIn",
    },
  },
};

const Header = () => {
  return (
    <motion.div
      className={s.container}
      variants={staggerContainer}
      initial="initial"
      whileInView="animate"
    >
      <NewAppBar />
      <div className="container">
        <div className="row">
          <div className="col-lg-5">
            <div className="d-flex flex-column h-100 justify-content-center px-2">
              <motion.h1 variants={fromLeftFade()}>
                Mohammad Moshin <br />{" "}
                <span className="text-center text-muted">PPM </span>
              </motion.h1>
              <motion.p className=" lead" variants={fromLeftFade()}>
                <blockquote className="blockquote fw-bold fst-italic">
                  “Fame Is The Perfume Of Heroic Deeds.”{" "}
                </blockquote>
                <p className="blockquote-footer">Socrates</p>
              </motion.p>
              <Link to="/all-posts/">
                <motion.button
                  className={s.content_btn}
                  variants={fromLeftFade("right")}
                >
                  Read More
                </motion.button>
              </Link>
            </div>
          </div>
          <div className="col-lg-7">
            <motion.img
              src="/images/cover.png"
              alt="feature_img"
              className="img-fluid mh-100"
              variants={featureImageScale}
              initial="initial"
              animate="animate"
            />
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default Header;
