import { FC, FormEvent, useState } from "react";
import Form from "react-bootstrap/esm/Form";
import { signInWithEmailAndPassword } from "firebase/auth";
import { authFire } from "../../firebase";
import { useAppDispatch } from "../../app/hooks";
import { setUser } from "./authSlice";
import Button from "react-bootstrap/esm/Button";
import { useNavigate } from "react-router-dom";
import { FirebaseError } from "firebase/app";
const Login: FC = () => {
  const dispatch = useAppDispatch();

  const isValidEmail = (email: string) => {
    return email.includes("@");
  };
  const isValidPassword = (password: string) => {
    return password.length > 6;
  };

  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [load, setload] = useState(false);
  const navigate = useNavigate();

  const [err, seterr] = useState<String | null>(null);
  const handleformData = async (e: FormEvent<HTMLElement>) => {
    e.preventDefault();
    if (isValidEmail(email) && isValidPassword(password)) {
      console.log("validate sucessfull");
      setload(true);

      signInWithEmailAndPassword(authFire, email, password)
        .then((result) => {
          const user = result.user;

          if (user) {
            dispatch(
              setUser({
                username: user.displayName ?? "no name",
                id: user.uid,
                email: user.email ?? "no email",
              })
            );

            setload(false);
            navigate("/");
          }
        })
        .catch((e: FirebaseError) => {
          console.log(e);
          setload(false);
          seterr(e.message);
        });
    }
  };

  return (
    <div className="container-fluid ">
      <div className="row min-vh-100">
        <div className="col-sm-4 m-auto shadow-lg p-5">
          <h2 className="text-center">Please Sign In</h2>
          <Form onSubmit={handleformData}>
            <Form.Group className="mb-3" controlId="formGroupEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter email"
                onChange={(e) => setemail(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formGroupPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                onChange={(e) => setpassword(e.target.value)}
              />
            </Form.Group>

            <Button type="submit" variant="secondary" className="w-100">
              Login
            </Button>

            <h2>{load ? "Loading" : ""}</h2>
            <div>{err}</div>
          </Form>
        </div>
      </div>
    </div>
  );
};

export default Login;
