import { FC } from "react";
import { useDispatch } from "react-redux";
import { useAppSelector } from "../app/hooks";
import { AboutItem, RecentPost } from "../components/About/About";
import Caroslider from "../components/caroSlider/Caroslider";
import Contact from "../components/Contact/Contact";
import Footer from "../components/Footer/Footer";
import Header from "../components/Header/Header";
import { fetchPosts, selectPosts } from "../features/Post/PostSlice";
import AsyncValue from "../components/AsyncValue/AsyncValue";

const HomePage: FC = () => {
  const { error, posts, status } = useAppSelector(selectPosts);
  const dispatch = useDispatch();

  return (
    <div>
      <Header />
      <AboutItem />
      <AsyncValue
        error={error}
        status={status}
        onRetry={() => dispatch(fetchPosts())}
      >
        <RecentPost title="Recent Posts" posts={posts.slice(0, 3)} />
      </AsyncValue>
      <Caroslider />
      <AsyncValue
        error={error}
        status={status}
        onRetry={() => dispatch(fetchPosts())}
      >
        <RecentPost
          title="Police Activity"
          posts={posts.filter((p) => p.catagories === "PoliceActivity")}
        />
      </AsyncValue>
      <Contact />
      <Footer />
    </div>
  );
};

export default HomePage;
